@import "@/assets/scss/variables";














































































$todo-height: 30px;

.TODOData {
  $root: &;

  position: relative;
  min-height: $todo-height;
  padding: 12px;
  word-break: break-word;
  background-color: mix($color-lightblue, white, 30%);
  border-radius: $border-radius-main;

  &__input {
    width: 100%;
    min-height: 30px;
  }

  &__button-close {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    padding: 8px;
    color: white;
    cursor: pointer;
    background-color: $color-red;
    border: none;
    border-radius: 50%;
    opacity: 0;
    transition: opacity 0.25s;
    transform: translate(-33%, 33%);

    #{$root}:hover & {
      opacity: 1;
    }
  }
}
